<template>
  <nav-bar id="nav-bar">
      <template v-slot:left>
        <img
          src="../../../assets/img/common/back.svg"
          alt=""
          @click="backClick"
          class="back"
        />
      </template>
      <template v-slot:center>
        <div class="title">
          <div class="title-item"
              v-for="(item, index) in titles"
              :key="index"
              @click="itemClick(index)"
              :class="{ active: index === currentIndex }">

              <span>{{ item }}</span>
          </div>
        </div>


      </template>
    </nav-bar>
</template>

<script>
import NavBar from '../../../components/common/navbar/NavBar.vue'
export default {
  name: 'DetailNavBar',
  data () {
    return {
      currentIndex: 0,
      titles: ["商品", "参数", "评论", "推荐"]
    }
  },
  components: {
    NavBar
  },
  methods: {
    itemClick(index) {
      this.currentIndex = index;
      this.$emit('tabClick', index )
    },
    backClick() {
      this.$router.back();
    },
  }
}
</script>

<style scoped>
#nav-bar {
  font-size: 14px;
  box-shadow: 0 1px 1px rgba(100, 100, 100, .2);
}

.title {
  display: flex;
}
.title-item{
  flex: 1;
  text-align: center;
}
.active {
  color: var(--color-high-text);
}
.active span {
  border-bottom: 2px solid var(--color-high-text);
    padding-bottom: 3px;
}
.back {
  margin-top: 12px;
}

</style>
