<template>
  <div class="goods">
    <!-- 引用子组件GoodsListItem 在模板标签中 遍历goods商品数据，然后将遍历到的商品数据利用 父子传值的方式 在子组件GoodsListItem中 用 props接收 -->
      <goods-list-item v-for="item in goods" :key="item.iid" :goodsItem="item" />
  </div>
</template>

<script>
import GoodsListItem from './GoodsListItem.vue'

export default {
  name: 'GoodsList',
  components: {
    GoodsListItem
  },
  props: {
    goods: {
      type: Array,
      default() {
        return []
      }
    }
  }
}
</script>

<style>
  .goods {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
</style>